import Vue from 'vue'
import Router from 'vue-router'
import Admin from '../components/Admin'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/admin/index/',
  routes: [
    {
      path: '/',
      name: 'Admin',
      component: Admin
    }
  ]
})



